import React, { Component } from 'react'
import ScrollAnimation from 'react-animate-on-scroll'

export default class Homebanner extends Component {
    render() {
        return (
                <div className='homeheader'>
                      <div className='row'>
                            
                            <div className='col-md-12'>
                                <ScrollAnimation animateIn='bounceInLeft'>
                                <div className='col-md-6 homeheader-about'>
                                    <h2>Enjoy Cheap, and <span className='bg-dark'>Seamless</span></h2>
                                    <h3>Rides.</h3>
                                    <h4>With a Nigerian Ride hailing service</h4>
                                </div>
                                </ScrollAnimation>
                                

                                <ScrollAnimation animateIn='bounceInRight'>
                                <div className='col-md-6 homeheader-right bg-dark'>
                                    <h2>Download the Envolv App</h2>
                                        <div>
                                            <div className = 'col-md-3'>
                                            <a href="#" target="_blank" rel="noopener noreferrer">
                                                <img src="https://upload.wikimedia.org/wikipedia/commons/3/3c/Download_on_the_App_Store_Badge.svg" alt=""/>
                                                </a>
                                            </div>

                                            <div className = 'col-md-3'>
                                            <a href="#" target="_blank" rel="noopener noreferrer">
                                                <img src="images/google-play-badge.png" alt=""/>
                                                </a>
                                            </div>
                                        </div>
                                </div>
                                </ScrollAnimation>
                            </div>
                      </div>
                  </div>
        )
    }
}
