import React, { Component, SyntheticEvent } from 'react'
import { NavLink, Link } from 'react-router-dom'
import '../main.css'

export default class Nav extends Component {
    state:any;
    constructor(props: any) {
    super(props);
    this.state = {
        isExpanded: false
      };
    
  }


  handleToggle(e: any) {
    e.preventDefault();
    this.setState({
      isExpanded: !this.state.isExpanded
    });
   
  }
  
    render() {
        
        const { isExpanded } = this.state;
        return (
            
                <nav className="navbar bg-dark">
                
                    <div className='navlogo'>
                    
                        <span><Link to={'/'}>Envovl</Link></span>
                        
                    </div>
                    <div className={`collapsenav ${isExpanded ? "is-expanded" : ""}`}>
                    <ul>
                        
                        <li><NavLink to={'/'}  exact> Home</NavLink></li>
                        <li><NavLink to={'/about'}> About</NavLink></li>
                        <li><NavLink to={'/services'}> Services</NavLink></li>
                        <div className='float-right'>
                        {/* <li ><NavLink to={'/about'}> Services</NavLink></li> */}
                        {/* <li ><NavLink to={'/about'}> Services</NavLink></li> */}
                        <li ><NavLink to={'/about'}> Products</NavLink></li>
                        </div>

                        
                    </ul>
                    </div>

                    <div className='navright clearfix'>
                        <ul>
                            {/* <li><NavLink to={'/products'} className= {`collapsenav text-white mt-2 ${isExpanded ? "" : "is-expanded text-white mt-2"}`}>products</NavLink> </li> */}
                            <li> <NavLink to={'/login'} className=' text-white mt-2'><i className="fa fa-user"></i> Login</NavLink> </li>
                            <li> <NavLink to={'/signup'} className="btn btn-default bg-light text-dark rounded-pill"> Sign up</NavLink> </li>

                        </ul>
                        <i
            className="fa fa-bars"
            aria-hidden="true"
            onClick={e => this.handleToggle(e)} />
                    </div>

                    
                
                
                </nav>
           
        )
    }
}
