import React, { Component } from 'react'
import Site from '../../../Site';
import ScrollAnimation from 'react-animate-on-scroll'
import { Link } from 'react-router-dom';

export default class Homebody extends Component {
    site = new Site();

    render() {
        return (
            <>
            <section className='container-fluid mt-5'>
                <h3>{this.site.sitename} for Business</h3>

                <p className='mt-5'>Trust in {this.site.sitename} to Power Your Business</p>
                <div className='row mt-5 envovlbusiness'>
                    <div className='col-md-4'>
                        <img src="images/business.png" alt=""/>
                        <h6>Work Meals</h6>
                        <p>Delight your employees with their favorite 
                            meals delivered directly to anywhere they work.</p>
                            <p className='mt-4'>
                                <a href="#"> Learn more</a>
                            </p>
                    </div>
                    <div className='col-md-4'>
                        <img src="images/business.png" alt=""/>
                        <h6>Employee rides</h6>
                        <p>Take the work out of work-related trips.
                             Create ground travel and commute programs for employees.</p>
                             <p className='mt-2'>
                                <a href="#"> Learn more</a>
                            </p>
                    </div>

                    <div className='col-md-4'>
                        <img src="images/business2.png" alt=""/>
                        <h6>Courtsey rides</h6>
                        <p >Aim to impress. Arrange door-to-door rides for your 
                            customers, clients, and guests..</p>
                            <p className='mt-4'>
                                <a href="#"> Learn more</a>
                            </p>
                    </div>
                </div>
            </section>

            <section className='darkbanner bg-dark'>
                <div className='container row col-md-12'>
                <ScrollAnimation animateIn='bounceInLeft'>
                <div className='col-md-6 mt-2 ml-5'>
                    <h2> Earn Extra Money Driving</h2>
                    </div>
                </ScrollAnimation>

                <div className='col-md-6 mt-2 ml-5'>
                    <p>
                        <Link to={'/signup/drive'}>
                        <button className='btn btn-defualt rounded-pill bg-white'>SIGN UP TO DRIVE</button>
                        </Link>
                    </p>
                    <p>
                    Set your own schedule, be your own boss.
                    </p>
                    </div>

                    
                </div>
            </section>


            <div className='container mt-5'>
                <div className='row'>

                      <div className='col-md-3 mt-4 ml-5'>
                          <img src="images/car-front.svg"  width='30%' alt=""/>
                        <div className='mt-3'>
                        <h6>Get a ride</h6>
                        <p>{this.site.sitename} offers you a ride in minutes.</p>
                        </div>
                    </div>

                      <div className='col-md-3 mt-5 ml-5'>
                          <img src="images/meter.svg" width='40%' alt=""/>
                          <div className='mt-3'>
                          <h6> The best prices</h6>
                          <p>We aim to offer the best ride prices in every city. See for yourself!</p>
                          </div>
                   
                    </div>

                      <div className='col-md-3 mb-1 ml-5'>
                          <img src="images/maps-phone.svg" alt="" width='20%' className='img-fluid'/>
                          <div className='mt-3'>
                          <h6> Easy to use</h6>
                          <p>Get wherever you need to go as quickly as possible.</p>
                          </div>
                    
                    </div>


  
                  
                  
                </div>
            </div>


            <div className='container signup-banner  mt-5'>
                <div className='row'>
                    <div className='col-md-6'>
                        <a href="signup/ride">
                        <h3><b>Sign up to ride</b> <span className='fa fa-mobile'></span>
                        <div></div>
                       
                         </h3>
                        
                        </a>
                    </div>

                    <div className='col-md-6'>
                        <a href="signup/ride" >
                        <h3><b>Sign up to drive</b> <span className='fa fa-car'></span>
                        <div>

                        </div>
                       
                         </h3>
                        
                        </a>
                    </div>
                </div>

            </div>
            </>
        )
    }
}
